.nav {
    grid-area: nav;
    background-color: #2c4b57;
    padding: 12px;

}
.item a {
    color: whitesmoke;
}

a:hover{
    text-decoration: underline;
    color: yellow;
}

.item {
    font-size: 16px;
    padding-bottom: 5px;
}

.active{
    font-weight: 800;
}
.friendsTitle{
    color: whitesmoke;
    padding-bottom: 8px;

}
.friends{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

}